.nav-bar{
    width: 100%;
    position:sticky;
    top:0;
    background-color: white;
    display: flex;
    flex-direction:row ;
    align-items: center;
    font-family: "Josefin Sans", sans-serif;
    border-bottom: 2px solid blueviolet;
    /* margin-bottom: 3rem; */
}

.nav-bar,.nav-right{
    display: flex;
    flex:3;
    justify-content: center;
    align-items: center;
}

.nav-left >img{
    width: 18rem;
    padding-left: 2rem;
}


.nav-center{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex:6;
}

.nav-center>ul{
    display: flex;
    flex-direction: row;
    justify-content: center;
    list-style: none;
}

.nav-center>ul>li {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: center;
    padding:20px;
    font-size: 1.1rem;
}


.nav-right>ul{
    display: flex;
}
.nav-right >ul>li{
    margin: 0px 25px;
    list-style: none;
    font-size: 23px;
}
.nav-right >ul>li:hover{
    font-size: 20px;
    font-weight: bold;
    transform: scale(1.3);
}
.nav-right >ul>li>a{
    text-decoration: none;
}

a{
    text-decoration: none;
    color: black;
}
a:visited {
    text-decoration: none;
    color: black;
  }
  