.header > img {
width: 100%;
height:100%;
}

.header-title{
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    background-color: rgb(140, 76, 201);
}


.header-title> h1{
    font-family: "Josefin Sans", sans-serif;
    font-weight: 300;
    color:whitesmoke;
    font-style: italic;
}


.btn {
    border-radius: 4px;
    background-color: #F34F83;
    font-family: "Josefin Sans", sans-serif;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 1.5rem;
    padding: 20px;
    width: 200px;
    transition: all 0.5s;
    cursor: pointer;
    margin-right: 25px;
  }
  
  .btn span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }

  a:link{
      color: white;
      text-decoration: none;
  }
 /* a:Link{
    text-decoration: none;
    color: white;
  }  */
  a:visited {
    text-decoration: none;
    color:white;
  }
  
  .btn span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .btn:hover span {
    padding-right: 25px;
  }
  
  .btn:hover span:after {
    opacity: 1;
    right: 0;
  }

